.main-title{
 color:#fff;
 font-size:56px;
 margin-bottom: 5px;
}
.subtitle{
  color:#fff;
  font-family: 'Nunito', sans-serif;

}
.fade{-webkit-animation:fadein 2s;animation:fadein 2s}@keyframes fadein{from{opacity:0}to{opacity:1}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}.parallax-effect{height:100vh;width:100%;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover}.hero{display:flex;align-items:center;justify-content:center;flex-direction:column;background-blend-mode:darken}

.cofeeCup{width:450px;display:flex;flex-direction:column;align-items:center;justify-content:center;padding-left:20px;padding-right:20px}.cofeeCup .cup-img{width:250px;height:250px}.cofeeCup h3{text-align:center;margin-bottom:20px}.cofeeCup p{margin-top:0;font-family:'Nunito', sans-serif;text-align:center;color:#767676;font-size:15px}

.titles{width:35%;align-items:center;justify-content:center;padding-left:250px;padding-bottom:80px}.section-titles{font-size:35px;text-align:center}.section-subtitles{font-size:17px;font-family:'Nunito', sans-serif;text-align:center}.black{color:#212529}.gray{color:#767676}

.part2{display:flex;justify-content:center;flex-direction:column;height:663px;padding:160px 0 130px 0;background-size:cover}.part2 .part2-cups{display:flex;justify-content:space-around;align-items:center;width:70%;align-self:center}

.ourStory{background-color:#1d140b;width:100%}.white{color:#fff}.story-title{padding-top:130px;margin-top:0}.story-image{width:100%;margin-top:-80px}

.info{width:50%;align-items:center;justify-content:center;text-align:justify;padding-left:100px;padding-right:100px;box-sizing:border-box}.info a{text-decoration:none;color:#212529}.info a:hover{cursor:pointer;font-weight:600}.info p{margin-top:0;font-family:'Nunito', sans-serif;color:#767676;font-size:15px}

.freshBeans{display:flex;justify-content:space-around;align-items:center;width:100%;background-color:#fff;overflow:hidden;margin:0;margin-top:-5px}.freshBeans div{background-size:cover}

.twocols{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}.container{width:50%;height:470px}

.icons-container{display:flex;justify-content:space-between;align-items:center;text-align:center;color:#daa37f}.best-coffee-container{background-position:right;background-repeat:no-repeat;background-size:cover;display:flex;justify-content:flex-start;margin-left:-120px;height:650px}

.gallery{display:flex;justify-content:center;height:500px;overflow:hidden}.gallery img{width:33.3%}

.blog-section{padding-top:100px;padding-bottom:100px;background-color:#ebebeb}.blog-gallery{display:flex;margin:auto;justify-content:space-evenly;width:85%;align-items:center}

.card{width:400px;min-height:520px;display:flex;flex-direction:column;justify-content:center;background-color:#fff;box-sizing:border-box;overflow:hidden}.card img{width:100%;height:260px}.card h3{text-align:left;margin-bottom:20px;padding-left:20px;padding-right:20px}.card p{margin-top:0;font-family:'Nunito', sans-serif;text-align:left;color:#767676;font-size:15px;padding-left:20px;padding-right:20px}

.contact{display:flex;width:100%;position:fixed;bottom:0;left:0;z-index:-1}.contact-hours{background-size:cover;background-repeat:no-repeat;background-position:left;padding-left:140px;padding-top:100px}.contact-hours div{width:115px}.contact-hours h4{color:#daa37f}.contact-hours p{color:#fff;font-family:'Nunito',sans-serif;font-size:12px}.contact-title{text-align:left}.contact-info{background-color:#111}

